<template>
    <div class="search-box">
        <div class="form-group order">
            <label> </label>
            <select v-model="allOrderStatus" @change="allOrder">
                <option disabled value="no">--    --</option>
                <option selected value="asc"></option>
                <option value="desc"></option>
            </select>
        </div>
        <div class="form-group search">
            <label> </label>
            <select v-model="searchCategory">
                <option selected value=""> </option>
                <option value=""> </option>
            </select>
            <input type="text" v-model="searchKeyword" @keypress.enter="search">
            <button @click="search"></button>
            <button @click="searchReset"></button>
        </div>
        
    </div>
</template>

<script>
export default {
    data() {
        return {
            searchCategory: "",
            searchKeyword: "",
            allOrderStatus: "asc"

        }
    },

    methods: {
        search() {
            this.$emit("search", this.searchCategory, this.searchKeyword)
        },
        searchReset() {
            this.$emit("search", this.searchCategory, "")
        },
        allOrder() {
            this.$emit("allOrder", this.allOrderStatus)
        }
    }
}
</script>

<style scoped>
    .search-box{
        padding: 10px;
    }

    .search-box .form-group * {
        margin: 0px 5px;
    }

    .form-group.order {
        float: left;
    }

    @media screen and (-webkit-min-device-pixel-ratio:0) { 
        .form-group.order {
            float: none;
        }
        .form-group {
            margin-bottom: 10px;
        }
    }

</style>